/* ==========================================================================
   WooCommerce Onsale and Out of Stock responsive styles - begin
   ========================================================================== */

@include ipad-portrait {
	
	.edgtf-woo-single-page {
		
		&.edgtf-woo-single-thumb-on-left-side {
			
			.edgtf-single-product-content {
				
				.edgtf-onsale,
                .edgtf-out-of-stock,
                .edgtf-new-product {
					left: 10px;
				}
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Onsale and Out of Stock responsive styles - end
   ========================================================================== */